import { FormHelperText } from '@material-ui/core';
import { KeyboardDatePicker } from '@material-ui/pickers';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { Controller } from 'react-hook-form';
import useDatePickerFieldStyle from './styles';
import Imgs from 'constants/images';

DatePickerField.propTypes = {
  form: PropTypes.object.isRequired, // important
  name: PropTypes.string.isRequired,

  label: PropTypes.string,
  disabled: PropTypes.bool,
  onChange: PropTypes.func,
  minDate: PropTypes.instanceOf(Date),
  maxDate: PropTypes.instanceOf(Date),
};

DatePickerField.defaultProps = {
  label: '',
  disabled: false,
  onChange: null,
  minDate: undefined,
  maxDate: undefined,
};

function DatePickerField(props) {
  const classes = useDatePickerFieldStyle();
  const { form, name, label, disabled, onChange, minDate, maxDate } = props;
  const { errors, formState } = form;
  const hasError = errors[name] ? true : false;
  const handleDateChange = () => {
    if (onChange) onChange();
  };

  return (
    <div>
      <Controller
        name={name}
        control={form.control}
        render={({ value, onChange, onBlur }) => (
          <KeyboardDatePicker
            onChange={(e) => {
              onChange(e);
              handleDateChange();
            }}
            id={name}
            label={label}
            className={classes.textField}
            error={!!hasError}
            variant="inline"
            format="dd/MM/yyyy"
            margin="normal"
            inputVariant="outlined"
            value={value}
            minDate={minDate}
            maxDate={maxDate}
            disableToolbar
            fullWidth
            autoOk
            keyboardIcon={<img src={Imgs.iconCalendar} />}
          />
        )}
        // as={<KeyboardDatePicker onChange={handleDateChange} />} // if want custom use render
        // id={name}
        // label={label}
        // className={classes.textField}
        // KeyboardButtonProps={{
        //   'aria-label': 'change date',
        // }}
        // error={!!hasError}
        // disableToolbar={disabled}
        // variant="inline"
        // format="dd/MM/yyyy"
        // margin="normal"
        // inputVariant="outlined"
      />
      {errors[name] && <FormHelperText className={classes.error}>{errors[name]?.message}</FormHelperText>}
    </div>
  );
}

export default DatePickerField;
