const appSlice = createSlice({
  name: 'app',
  initialState: {
    provinces: [],
    districts: [],
    shops: [],
    openHambuger: false,
  },
  reducers: {
    toggleHambuger(state, action) {
      try {
        state.openHambuger = !state.openHambuger;
      } catch (error) {}
    },

    closeHambuger(state, action) {
      try {
        state.openHambuger = false;
      } catch (error) {}
    },
  },
  extraReducers: {
    // [getProvinces.fulfilled]: (state, action) => {
    //   if (action.payload) state.provinces = action.payload.data;
    // },
  },
});

export const { toggleHambuger, closeHambuger } = appSlice.actions;
export default appSlice.reducer;
