import React from 'react';
import { Route, Switch, useRouteMatch } from 'react-router-dom';
import InformationUser from './pages/InformationUser';
import { RoutersInfor } from 'constants/routers';
import { InformationContactFamily, VideoFace, AuthencationId } from './ComponentLoadable';
import { StepHeader } from 'components/StepHeader';
import Box from '@material-ui/core/Box';
import { Colors } from 'constants/theme';

InformationFeature.propTypes = {};

function InformationFeature(props) {
  const match = useRouteMatch();

  return (
    <div style={{ background: Colors.c_f6f7fb }}>
      <StepHeader />
      <Box style={{ padding: '16px 18px' }}>
        <Switch>
          <Route path={match.path} component={InformationUser} exact />
          <Route path={`${match.path}${RoutersInfor.INFOR_CONTACT_FAMILY}`} component={InformationContactFamily} />
          <Route path={`${match.path}${RoutersInfor.VIDEO_FACE}`} component={VideoFace} />
          <Route path={`${match.path}${RoutersInfor.UPLOAD}`} component={AuthencationId} />
        </Switch>
      </Box>
    </div>
  );
}

export default InformationFeature;
