import { TextField } from '@material-ui/core';
import PropTypes from 'prop-types';
import React from 'react';
import { Controller } from 'react-hook-form';
import useInputFieldStyle from './styles';

InputField.propTypes = {
  form: PropTypes.object.isRequired, // important
  name: PropTypes.string.isRequired,

  label: PropTypes.string,
  disabled: PropTypes.bool,
};

function InputField(props) {
  const classes = useInputFieldStyle();
  const { form, name, label, disabled } = props;
  const { errors, formState } = form;
  const hasError = errors[name] ? true : false;

  return (
    <Controller
      name={name}
      control={form.control}
      as={
        <TextField
          className={classes.root}
          InputLabelProps={{ className: classes.label }}
          inputProps={{ className: classes.input }}
          InputProps={{ className: classes.inputOutline }}
        />
      } // if want custom use render
      // bind attributes to TextField
      variant="outlined"
      fullWidth
      margin="normal"
      label={label}
      disabled={disabled}
      error={!!hasError}
      helperText={errors[name]?.message}
    ></Controller>
  );
}

export default InputField;
