import React from 'react';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import Box from '@material-ui/core/Box';
import { useStylesLineInformation } from './styles';

LineInformation.propTypes = {
  title: PropTypes.string,
  content: PropTypes.string,
};
LineInformation.defaultProps = {
  title: '',
  content: '',
};

function LineInformation(props) {
  const { title, content } = props;
  const classes = useStylesLineInformation();
  return (
    <Box className={classes.root}>
      <Typography variant="subtitle1" className={classes.title}>
        {title}
      </Typography>
      <Typography variant="h1" className={classes.content}>
        {content}
      </Typography>
    </Box>
  );
}

export { LineInformation };
