import React from 'react';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { ColorlibConnector, useColorlibStepIconStyles } from './styles';
import Imgs from 'constants/images';

function ColorlibStepIcon(props) {
  const { isShowIcon } = props;
  const classes = useColorlibStepIconStyles();
  const { active, completed } = props;

  const icons = {
    1: <img src={Imgs.iconStep1} />,
    4: <img src={Imgs.iconStep2} />,
    7: <img src={Imgs.iconStep3} />,
  };

  return (
    <div
      className={clsx(!isShowIcon ? classes.none : classes.root, {
        [classes.active]: active,
        [classes.completed]: completed,
      })}
    >
      {icons[String(props.icon)]}
    </div>
  );
}
function getSteps() {
  return [
    { isShow: true, label: 'Thng tin' },
    { isShow: false, label: '' },
    { isShow: false, label: '' },
    { isShow: true, label: 'Ti sn' },
    { isShow: false, label: '' },
    { isShow: false, label: '' },
    { isShow: true, label: 'Gii ngn' },
  ];
}
StepHeader.propTypes = {
  activeStep: PropTypes.number,
  steps: PropTypes.array,
};
StepHeader.defaultProps = {
  activeStep: 5,
  steps: getSteps(),
};

function StepHeader(props) {
  const { activeStep, steps } = props;
  //   const [activeStep, setActiveStep] = React.useState(3);
  return (
    <>
      <Stepper alternativeLabel activeStep={activeStep} connector={<ColorlibConnector />}>
        {steps.map((item) => (
          <Step key={item.label}>
            <StepLabel StepIconComponent={(props) => <ColorlibStepIcon {...props} isShowIcon={item.isShow} />}>
              {item.label}
            </StepLabel>
          </Step>
        ))}
      </Stepper>
    </>
  );
}
export { StepHeader };
