import { FormHelperText, MenuItem, Select } from '@material-ui/core';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Controller } from 'react-hook-form';
import useSelectFieldStyles from './styles';
import Imgs from 'constants/images';

SelectField.propTypes = {
  form: PropTypes.object.isRequired, // important
  name: PropTypes.string.isRequired,
  googleSans: PropTypes.bool,

  label: PropTypes.string,
  disabled: PropTypes.bool,
  data: PropTypes.array,
  onChange: PropTypes.func,
};

SelectField.defaultProps = {
  label: '',
  disabled: false,
  data: [],
  onChange: null,
  googleSans: false,
};

const IconSelect = () => {
  return <img src={Imgs.iconArrowBottom} style={{ marginRight: 13 }} />;
};

function SelectField(props) {
  const classes = useSelectFieldStyles();
  const { form, name, label, disabled, data, onChange, googleSans } = props;
  const { errors, formState } = form;
  const hasError = errors[name] ? true : false;

  const handleChange = (e) => {
    if (onChange) onChange(e.target.value);
  };

  return (
    <div>
      <FormControl variant="outlined" className={classes.select} error={hasError}>
        <InputLabel>{label}</InputLabel>
        <Controller
          name={name}
          control={form.control}
          render={({ value, onChange, onBlur }) => (
            <Select
              id={name}
              label={label}
              disabled={disabled}
              value={value}
              onBlur={onBlur}
              onChange={(e) => {
                onChange(e);
                handleChange(e);
              }}
              MenuProps={{
                getContentAnchorEl: null,
                anchorOrigin: {
                  vertical: 'bottom',
                  horizontal: 'left',
                },
              }}
              inputProps={{ className: classes.input }}
              className={classes.root}
              IconComponent={IconSelect}
            >
              {data.map((item) => (
                <MenuItem className={googleSans && classes.menuItem} key={item.value} value={item.value}>
                  {item.text}
                </MenuItem>
              ))}
            </Select>
          )}
        />

        {errors[name] && <FormHelperText>{errors[name]?.message}</FormHelperText>}
      </FormControl>
    </div>
  );
}

export default SelectField;
