import { Typography, Button } from '@material-ui/core'
import React from 'react'
import useBlockImgStyles from './style';
import iconCamera from '../../../../assets/images/iconCamera.png';
import { Colors } from 'constants/theme';
import { useDropzone } from 'react-dropzone';
function BlockImg({ title, img, error, checkError, onChange, keyWork }) {
    const classes = useBlockImgStyles();
    const { getRootProps, getInputProps } = useDropzone({
        accept: 'image/jpeg, image/png',
        onDrop: file => {
            let reader = new FileReader()
            reader.readAsDataURL(file[0]);
            reader.onload = () => {
                let fileInfo = {
                    name: file[0].name,
                    type: file[0].type,
                    size: Math.round(file[0].size / 1000) + ' kB',
                    base64: reader.result,
                    file: file[0]
                };
                onChange({data: fileInfo,key: keyWork});
                setUrlImg(fileInfo.base64);
            }
        }
    });
    const [urlImg, setUrlImg] = React.useState(null);
    return (
        <div>
            <div className={classes.root}>
                <div {...getRootProps({ className: 'dropzone' })}>
                    <input {...getInputProps()} />
                    <Typography className={classes.txtTitle}>{title}</Typography>
                    <Button
                        aria-controls="simple" aria-haspopup="true"
                        classes={{
                            root: checkError ? classes.boxError : classes.boxContainer,
                            label: classes.label,
                        }}
                        className={classes.button}
                        color={Colors.white} >
                        {
                            urlImg ? 
                            <div>
                                <img src={urlImg || img} alt="Logo" className={classes.imgPicker} />
                                <img src={iconCamera} alt="icon camera" className={classes.imgCameraAbsolute} />
                            </div> :
                                <div className={classes.imgContainer}>
                                    <img src={img} alt="Logo" className={classes.imgLogo} />
                                    <img src={iconCamera} alt="icon camera" className={classes.imgCamera} />
                                </div>
                        }
                    </Button>
                </div>
            </div>
            {checkError && <Typography className={classes.errorNonUpload}>{error}</Typography>}
        </div>

    )
}
export default BlockImg;
