import React from 'react';
import Typography from '@material-ui/core/Typography';
import PropTypes from 'prop-types';
import { useStylesDescriptionHeader } from './styles';

DescriptionHeader.propTypes = {
  children: PropTypes.node.isRequired,
  variant: PropTypes.string,
  className: PropTypes.string,
  style: PropTypes.object,
};
DescriptionHeader.defaultProps = {
  variant: 'h6',
  style: {},
};

function DescriptionHeader(props) {
  const { children, variant, className, style } = props;
  const classes = useStylesDescriptionHeader();
  return (
    <Typography variant={variant} className={`${classes.root} ${className}`} style={style}>
      {children}
    </Typography>
  );
}

export { DescriptionHeader };
