import Routers from 'constants/routers';
import HomeFeature from 'features/Home';
import InformationFeature from 'features/Information';
import IntroFeature from 'features/Intro';
import LicenseFeature from 'features/License';
import LoanFeature from 'features/Loan';
import { Route, Switch } from 'react-router-dom';

const routes = () => (
  <Switch>
    <Route path={Routers.INTRO} component={IntroFeature} />
    <Route path={Routers.HOME} component={HomeFeature} />
    <Route path={Routers.INFOR} component={InformationFeature} />
    <Route path={Routers.LOAN} component={LoanFeature} />
    <Route path={Routers.LICENSE} component={LicenseFeature} />
    {/* <Route component={MainLayout}></Route> */}
  </Switch>
);

export default routes;
